.coin-container{
    display: flex;;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height:80px;
    border-bottom: 1px solid #d7d7d7;
    width: 990px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height:30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display: flex;    
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

.coin-price{
    width: 150px;
}

.coin-volume{
    width: 230px;
}

.coin-percent{
    width: 100px;
}

.red{
    color: #f00606;
}

.green{
    color:darkgreen;
}

.coin-marketcap{
    width: 155px;
}